import * as types from '../mutation-types';

const state = {
    life: {
        Power: false,
        Water: false,
        Gas: false,
        Heat: false,
        Television: false
    },
    historyList: [
        {
            id: '01',
            area: "03",
            companyName: '3',
            company: "033",
            href: "LifePowerInfo",
            item: "",
            name: "",
            money: '123.45',
            no: "5654654"
        }, {
            id: '02',
            area: "03",
            company: "032",
            companyName: '2',
            href: "LifeHeatInfo",
            item: "",
            name: "",
            mark: '',
            money: '234.56',
            no: "3456754"
        }] // historyList
};

// getters
const getters = {
    history: state => state.life,   // 
    historyList: state => state.historyList
};

// actions
const actions = {
    showHistory: ({ commit }, data) => {
        commit('HISTORY', data);
    },
    setHistory: ({ commit }, data) => {
        commit('SET_HISTORY', data);
    }
};

// mutations
const mutations = {
    HISTORY(state, data) {
        state.historyList.push(data);
    },
    SET_HISTORY(state, data) {
        state.life[data.item] = data.status;
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



